body {
  color: #333;
  background-color: #fff;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

header {
  color: #fff;
  text-align: center;
  background-color: #4caf50;
  padding: 1rem;
}

header h1 {
  margin-bottom: .5rem;
  font-size: 2rem;
}

header p {
  font-size: 1.1rem;
}

nav ul {
  background-color: #423f3f;
  justify-content: center;
  list-style-type: none;
  display: flex;
}

nav ul li {
  margin: 0 10px;
}

nav ul li a {
  color: #fff;
  padding: 10px 15px;
  text-decoration: none;
  display: block;
}

nav ul li a:hover {
  background-color: #4caf50;
}

main {
  background-color: #fff;
  max-width: 900px;
  margin: auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

main img {
  border-radius: 8px;
  max-width: 100%;
  height: auto;
  margin: 1rem auto;
  display: block;
}

main ul {
  padding: 0;
  list-style-type: none;
}

main ul li {
  background-color: #fff;
  border-left: 4px solid #ffeb3b;
  margin-bottom: 10px;
  padding: 10px;
  transition: background-color .3s;
}

main ul li:hover {
  background-color: #4caf4f5e;
}

main ul li a {
  color: #4caf50;
  font-weight: bold;
  text-decoration: none;
}

h2 {
  color: #4caf50;
  margin-top: 1.5rem;
  font-size: 1.3rem;
}

p, ul li {
  margin-bottom: 1rem;
}

#map1, #map2, #map3 {
  width: 100%;
  height: 400px;
  margin-top: 20px;
}

@media (width <= 768px) {
  nav ul {
    flex-direction: column;
  }

  nav ul li {
    text-align: center;
    margin: 0;
  }

  header h1 {
    font-size: 1.5rem;
  }

  header p {
    font-size: 1rem;
  }
}
